import React from 'react'
import Carousels from '../Carousel/Carousels'

import {FourthSec,FourthSecRow,FourthSecCol,FourthHeading} from './FourthSectionElements'

const FourthSection = () => {
    return (        
        <FourthSec className='container-fluid'>
            <FourthSecRow className="row">
                <FourthSecCol className="col-xs-2 col-sm-2 col-md-2">
                    <FourthHeading>
                        Now more than ever
                    </FourthHeading>
                </FourthSecCol>
                <FourthSecCol className="col-sm-10 col-md-10">                 
                    <Carousels/>
                </FourthSecCol>
            </FourthSecRow>
        </FourthSec>
    )
}

export default FourthSection
