import React from 'react'
import {
    BrudcumbsContainer,
    BrudcumbsRow,
    BrudcumbsCol,
    BrudcumbsUl,
    BrudcumbsLi,
    BrudcumbsA
} from './BrudcumbsElements'

const brudcrumbCont = [
    {
        name: 'Test Run',
        link: '#'
    },
    {
        name: '->'
    },
    {
        name: 'Needs',
        link: '#'
    }
]

const Brudcumbs = () => {
    return (
        <BrudcumbsContainer>
            <BrudcumbsRow>
                <BrudcumbsCol>
                    <BrudcumbsUl>
                        {
                            brudcrumbCont.map((item, key)=>{
                                return <BrudcumbsLi key={key}><BrudcumbsA href={item.link}>{item.name}</BrudcumbsA></BrudcumbsLi>
                            })  
                        }
                    </BrudcumbsUl>
                </BrudcumbsCol>
            </BrudcumbsRow>
        </BrudcumbsContainer>
    )
}

export default Brudcumbs
