import React from 'react'
import { ThirdSec, ThirdSectioncontainer, ThirdSectioncontainerRow, ThirdSectioncontainerCol, ThirdSectioncontainerImg, ThirdSectioncontainerHeading, ThirdSectioncontainerP, ThirdSectioncontainerImgContainer } from './ThirdSectionElements'
import ThiredSecImg from './images/pexels-tara-winstead-8386440.png'
const ThirdSection = () => {
    return (
        <ThirdSec>
            <ThirdSectioncontainer className="container">
                <ThirdSectioncontainerRow className="row">
                    <ThirdSectioncontainerCol className="col-md-6">
                        <ThirdSectioncontainerImgContainer>
                            <ThirdSectioncontainerImg src={ThiredSecImg} style={{ width: '100%' }} />
                        </ThirdSectioncontainerImgContainer>
                    </ThirdSectioncontainerCol>
                    <ThirdSectioncontainerCol className="px-5 col-md-6">
                        <ThirdSectioncontainerHeading>
                            One (platform) for all.
                        </ThirdSectioncontainerHeading>
                        <ThirdSectioncontainerP>
                            We are bridging the gap between the customer, consultant, value-added reseller (VAR) and vendor whilst significantly reducing the sales-cycle including costs and manpower. Our AI powered platform predicts future market trends, provides business insights, improves a customers’ IT infrastructure, and helps increase (compute) performance in a blink of an eye. Accessible at your fingertips using a desktop, laptop, tablet or smartphone.
                        </ThirdSectioncontainerP>
                    </ThirdSectioncontainerCol>
                </ThirdSectioncontainerRow>
            </ThirdSectioncontainer>
        </ThirdSec>
    )
}

export default ThirdSection
