import React from 'react'
import FiveImg from './images/About-us.png'
import {FiveSection,FiveSectionRow,FiveSectionCol,FiveSectionImg,FiveSectionHeading,FiveSectionP} from './FiveSecElements'
const FiveSec = () => {
    return (
        <FiveSection id='about'>
            <FiveSectionRow className="row">
                <FiveSectionCol className="col-md-6" style={{textAlign: 'center'}}>
                    <FiveSectionImg src={FiveImg}/>
                </FiveSectionCol>
                <FiveSectionCol className="col-md-6" style={{padding: '3rem'}}>
                    <FiveSectionHeading>About us</FiveSectionHeading>
                    <FiveSectionP>
                        Based in the City of London, Nimblr Data is an independent Software as a Service(SaaS) company that is solely focused on servicing the Banking, Financial (Services) and Fintech industry. By collaborating with strategic partners among which leading value-added resellers (VARs), vendors, and IT consultancy firms that have insight and knowledge of both the Information Technology and Financial sector, we are developing a unified platform that will enable us to work together and use time more effectively.
                    </FiveSectionP>
                </FiveSectionCol>
            </FiveSectionRow>
        </FiveSection>
    )
}

export default FiveSec
