import React from 'react';
import FirstSection from '../Components/FirstSection/FirstSection';
import FiveSec from '../Components/FiveSec/FiveSec';
import FourthSection from '../Components/FourthSection/FourthSection';
import LogoSec from '../Components/Logo/LogoSec';
import Nav from '../Components/Nav/Navgation/Nav';
import SecondSection from '../Components/SecondSection/SecondSection';
import ThirdSection from '../Components/ThirdSection/ThirdSection';
import VideoIcon from './images/arrow.png'
import {FirstSec,
    FirstSecCol1,
    FirstSecCol2,
    VideoLink,
    VideoLinkText,
    VideoLinkBox,
    ArrowImg,
    VideoLinkHref
    } from './HomeElement'
import SixthSection from '../Components/SixthSection/SixthSection';
import SeventhSec from '../Components/SeventhSection/SeventhSec';
import Contact from '../Components/Contact/Contact';

const Home = () => {
    return (
        <div className='container-fluid'>
           <FirstSec className='row'>
                <FirstSecCol1 className='col-lg-6 col-md-12 col-sm-12' >
                    <LogoSec/>
                    <VideoLinkHref href="/">
                        <VideoLink>
                            <VideoLinkText>
                                Introduction Video
                            </VideoLinkText>
                            <VideoLinkBox>
                                <ArrowImg src={VideoIcon} style={{width: '100%'}}/>
                            </VideoLinkBox>
                        </VideoLink>
                    </VideoLinkHref>
                </FirstSecCol1>
                <FirstSecCol2 className='col-lg-6 col-md-12 col-sm-12'>
                    <Nav/>
                    <FirstSection/>
                </FirstSecCol2>
           </FirstSec>           
           <SecondSection/>
           <ThirdSection/>
           <FourthSection/>
           <FiveSec/>
           <SixthSection/>
           <SeventhSec/>
           <Contact/>
        </div>
    )
}

export default Home
