import React from 'react'
import { useLocation } from 'react-router-dom'

import {
    FirstSecNav,
    FirstSecUl,
    FirstSecLi,
    FirstSecLiA,
    FirstSecLiBtn,
    FirstSecLiBtnA,
    FirstSecHome
} from '../../../pages/HomeElement'
const Nav = () => {

    const loca = useLocation();
    return (
        <FirstSecNav className="d-none d-md-none d-lg-block">
            {
                loca.pathname === '/' ? <FirstSecUl>
                    <FirstSecLi><FirstSecLiA activeClass='active' to='about' spy={true} smooth={true} offset={0} duration={500} >About us</FirstSecLiA></FirstSecLi>
                    <FirstSecLi><FirstSecLiA activeClass='active' to='price' spy={true} smooth={true} offset={0} duration={500} >Pricing </FirstSecLiA></FirstSecLi>
                    <FirstSecLi><FirstSecLiA activeClass='active' to='contact' spy={true} smooth={true} offset={0} duration={500} >Contact Us</FirstSecLiA> </FirstSecLi>
                    <FirstSecLiBtn><FirstSecLiBtnA to='/login'>Login / Sign up</FirstSecLiBtnA></FirstSecLiBtn>
                </FirstSecUl> : <FirstSecUl>
                    <FirstSecLiBtn><FirstSecLi><FirstSecHome to='/'>Home</FirstSecHome></FirstSecLi><FirstSecLiBtnA to='/login'>Login / Sign up</FirstSecLiBtnA></FirstSecLiBtn>
                </FirstSecUl>
            }

        </FirstSecNav>
    )
}

export default Nav
