import styled from "styled-components";


export const ThirdSec = styled.div`
    background: url(./images/123.png);
    padding: 4.5rem 2rem;
    overflow: hidden;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
`

export const ThirdSectioncontainer = styled.div`
    background: linear-gradient(
    317deg
    , #ffffff2e, #ffffff14);
    border: 1px solid #ffffff29;
    border-radius: 1.5rem;
    backdrop-filter: blur(2rem);
    padding: 4.2rem;
    @media screen and (max-width: 800px){
        padding: 0rem;
    }
`

export const ThirdSectioncontainerRow = styled.div`
`

export const ThirdSectioncontainerCol = styled.div`
    @media screen and (max-width: 800px){
        padding: 0px;
    }
`

export const ThirdSectioncontainerHeading = styled.h3`
    font-size: 3rem;
    color: #372B7B;
    font-weight: bold;
`
export const ThirdSectioncontainerP = styled.p`
    font-size: 2.6rem;
    line-height: 4rem;
    margin-top: 2rem;
    font-weight: 300;
`
export const ThirdSectioncontainerImgContainer = styled.div`
`
export const ThirdSectioncontainerImg = styled.img`
`