import React from 'react'
import { SecondSec, SecondSecRow, SecondSecCol, SecondSecCol1, SecondSecColBox, SecondSecImg, SecondHeading, SecondSecStripImg } from '../SecondSection/SecondSectionElement'
import stripImg from './Stripe_img.svg'

const SecondSecData = [
    {
        name: 'Store',
        image: '../../../images/cloud-storage.png'
    },
    {
        name: 'Manage',
        image: '/images/research.png'
    },
    {
        name: 'Analyze',
        image: '/images/data-analysis.png'
    },
    {
        name: 'Monetize',
        image: '/images/money-bag.png'
    }
]

const SecondSection = () => {
    return (
        <SecondSec className="container-fluid" style={{'padding':'0px'}}>
            <SecondSecRow className="row">
                <SecondSecCol1 className="d-none d-md-none d-lg-block col-md-4">
                    <SecondSecStripImg src={stripImg}/>
                </SecondSecCol1>
                {SecondSecData.map((item, index)=>{
                    return<SecondSecCol className="col-sm-2 col-md-3 col-lg-2">
                    <SecondSecColBox>
                        <SecondSecImg src={item.image}/>
                        <SecondHeading>{item.name}</SecondHeading>
                    </SecondSecColBox>                
                </SecondSecCol>})}                
            </SecondSecRow>
        </SecondSec>
    )
}

export default SecondSection
