// import Navbar from "./Components/Nav/Navbar";
import Home from "./pages/Home";
import Login from './pages/auth/Login/Loin'
import { Route, Switch } from 'react-router-dom'
import PageNotFound from "./pages/PageNotFound";
import BookADemo from "./pages/BookADemo/BookADemo";
import Signup from "./pages/auth/Signup/Signup";
import TermsAndCondiction from "./pages/TermsAndCondiction";
import FAQ from "./pages/FAQS/FAQ";
import Dashboard from "./pages/Dashboard/Dashboard";
import NeedsAIAnalysisPage from "./pages/AIAnalysisPage/NeedsAIAnalysisPage";
import ITInfrastructure from "./pages/AIAnalysisPage/ITInfrastructure";
import ComputerHardwarePage from "./pages/AIAnalysisPage/ComputerHardwarePage";
import ContactSupportPage from "./pages/ContactSupport/ContactSupportPage";

function App() {
  return (
    <>
      <Switch>
        <Route path="/" component={Home} exact />
        <Route path="/login" component={Login} exact />
        <Route path="/signup" component={Signup} exact />
        <Route path="/bookademo" component={BookADemo} exact />
        <Route path="/faq" component={FAQ} exact />
        <Route path="/ai-suazor/ai-analysis" component={Dashboard} exact />
        <Route path="/ai-suazor/needs" component={NeedsAIAnalysisPage} exact />
        <Route path="/ai-suazor/it-infrastructure" component={ITInfrastructure} exact />
        <Route path="/ai-suazor/computer-hardware" component={ComputerHardwarePage} exact />
        <Route path="/ai-suazor/contact" component={ContactSupportPage} exact />
        <Route path="/terms-conditions" component={TermsAndCondiction} exact />
        <Route component={PageNotFound} />
        
      </Switch>
    </>
  );
}

export default App;
