import React, { useState } from 'react';
import {SildeBarContainer, SildeBarUl, SildeBarLi, SildeBarA, SideBarImg} from './SildeBarElements'
import Icon1 from './icon_dashboard.svg'
import Icon1Hover from './icon_dashboard_hover.svg'
import Icon2 from './icon_report.svg'
import Icon2Hover from './icon_report_hover.svg'
import Icon3 from './icon_aianalysis.svg'
import Icon3Hover from './icon_aianalysis_hover.svg'
import Icon4 from './icon_profile.svg'
import Icon4Hover from './icon_profile_hover.svg'
import Icon5 from './icon_ notification.svg'
import Icon5Hover from './icon_ notification_hover.svg'
import './iconscolor.css'


const SildeBar = () => {
    const sideicons = [
        {
            icon: Icon1,
            iconHover: Icon1Hover,
            title: 'title1'
        },
        {
            icon: Icon2,
            iconHover: Icon2Hover,
            title: 'title2'
        },
        {
            icon: Icon3,
            iconHover: Icon3Hover,
            title: 'title3'
        },
        {
            icon: Icon4,
            iconHover: Icon4Hover,
            title: 'title3'
        },
        {
            icon: Icon5,
            iconHover: Icon5Hover,
            title: 'title3'
        }
    ];

    const [isShown, setIsShown] = useState(true);



    return (        
        <SildeBarContainer>
            <SildeBarUl>
                {
                sideicons.map((item, key)=>{
                    return <SildeBarLi key={key}>
                        <SildeBarA><SideBarImg src={isShown==true? item.icon: item.iconHover } width='80%' /></SildeBarA>
                    </SildeBarLi>
                })
                }
            </SildeBarUl>
        </SildeBarContainer>
    )
}

export default SildeBar
