import styled from "styled-components";
import { Link } from "react-router-dom";
import { Link as RLink } from 'react-scroll'


export const FirstSec = styled.div`
    min-height: 100vh;    
`

export const FirstSecHome = styled(Link)`
     display: inline-block;
    padding: 2rem 3rem;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    font-size: 2rem;
    color: #1A1A1A;
    font-weight: 500;
    &:hover{
        color: #DD5454;
    }
    @media screen and (max-width:1329px){
        padding: 2rem 1rem!important;
    }
    @media screen and (min-width:1330px){
        padding: 2rem 3rem!important;
    }
`

export const FirstSecCol1 = styled.div`
    min-height: 100vh;
    background-image: url("./images/Weblandingpage.png");
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    background-position: center;
    :after{
        width:6rem;
        height: 100%;
        position:absolute;
        top: 0px;
        right: 0px;
        content: '';
        border-radius: 4rem 0rem 0rem 0rem;
        background: white;
    }
    @media screen and (max-width:1148px){
        min-height: 45vh;
        &:before{        
            content: '';
            width: 100vw;
            height: 3.5rem;
            background: white;
            position: absolute;
            bottom: -1px;
            left: 6;
            border-radius: 5rem 5rem 0 0;
        }
    }

    @media screen and (max-width:991px){
        &:after{
            display: none;
        }
    }
`

export const FirstSecCol2 = styled.div`
    min-height: 102%;
    background: white;

    @media screen and (max-width:1148px){            
        position: static;
        display: block;
        text-align: center;
    }
`

export const FirstSecNav = styled.div`
    width: 100%;
    margin-top: 5rem;
    margin-bottom: 5rem;
    @media screen and (max-width:1280px){
        margin-bottom: 4rem;
    }
`

export const FirstSecUl = styled.ul`
    text-align:right;
`
export const FirstSecLi = styled.li`
    display: inline-block;
    text-align: center;
`
export const FirstSecLiA = styled(RLink)`
    display: inline-block;
    padding: 2rem 3rem;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    font-size: 2rem;
    color: #1A1A1A;
    font-weight: 500;
    &:hover{
        color: #DD5454;
    }
    @media screen and (max-width:1329px){
        padding: 2rem 1rem!important;
    }
    @media screen and (min-width:1330px){
        padding: 2rem 3rem!important;
    }
`
export const FirstSecLiBtn = styled.li`
    display: inline-block;
    text-align: center;
`
export const FirstSecLiBtnA = styled(Link)`
    display: inline-block;
    padding: 1rem 3rem;
    text-align: center;
    text-decoration: none;
    cursor:pointer;
    font-size: 2rem;
    color: #FFFFFF;
    background: #372B7B;
    border-radius: 1.5rem;
    margin-right: 3rem;
    margin-left: 2rem;
    &:hover{
        background: #DD5454;
        color: #ffffff80;
    }

    @media screen and (max-width:1414px){        
        margin-left: 0rem;
        padding: 1rem!important;
    }
    @media screen and (max-width:1200px){        
        margin-left: 0rem;
        padding: 1rem!important;
    }
`

export const FirstSecSlide = styled.div`
    text-align: left;
`
export const FirstSecSlideH1 = styled.h1`
    font-size: 6.5rem;
    color: #372B7B;
    margin-left: 11.5rem;
    font-weight: bold;
    text-align:left;
    line-height: 10rem;

    @media screen and (max-width:1024px){   
        font-size: 4rem;      
        margin-left: 0rem;
        padding:0px;
        text-align:center;        
        line-height: 5rem;
     }

    @media screen and (max-width:1600px){   
        font-size: 5rem;
        margin-left: 0rem;
        padding: 0px;
        text-align: left;
        line-height: 6.5rem;
        margin-left: 9rem;
     }

    @media screen and (max-width:767px){   
        font-size: 3rem;      
        margin-left: 0rem;
        padding:0px;
        text-align:center;
     }
`
export const FirstSecSlideDiv = styled.div`
    width: 70%;
    background: linear-gradient( 313deg ,#8e8e8e30,#ffffff52);
    border-radius: 2rem;
    padding: 2rem 3rem;
    margin-left: 9.1rem;
    margin-top: 3.5rem;
    -webkit-backdrop-filter: blur(0.5rem);
    border: 1px solid #dedede;
    backdrop-filter: blur(0.5rem);
    @media screen and (max-width: 1148px){
        width: 100%;
        margin-left: 0px;
    }
`
export const FirstSecSlideH4 = styled.h4`
    font-size: 2rem;
    line-height: 3.5rem;
`
export const FirstSecSlideCol = styled.div`
    
`
export const FirstSecSlideRowCol = styled.div`
    
`
export const FirstSecSlideRow = styled.div`
    margin-top: 2rem;
`
export const BtnStyle = styled.button`
    width: 100%;
    margin: auto;
    border: none;
    font-size: 1.5rem;
    padding:1.5rem;
    border-radius: 1rem;
    font-weight: bold;
    background: #DD5454;
    color: #fff;
    transition: all 1s ease;

    &:hover{        
        background: #c51e1e;
    }
`
export const BtnStyle2 = styled.button`
    width: 100%;
    margin: auto;
    border: none;
    font-size: 1.5rem;
    padding:1.5rem;
    border-radius: 1rem;
    font-weight: bold;
    background: #372B7B;
    color: #fff;
    transition: all 1s ease;

    &:hover{
        background: #191050;
    }
`

export const Logo = styled.div`
    width: 3rem;
    height: 3rem;
    background-color: red;
`

export const VideoLink = styled.div`
    display: flex;
    height: 18.7rem;
    width: 95%;
    position: absolute;
    bottom: 0px;
    text-align: center;
    justify-content: center;
    align-items: center;
    border-radius: 3rem 3rem 0rem 0rem;
    background: linear-gradient(45deg, #ffffff6b, #ffffff0f);
    backdrop-filter: blur(2rem);
    @media screen and (max-width: 1148px){
        display: none;
    }
`

export const VideoLinkText = styled.h3`
    font-size: 4.5rem;
    color: #fff;
    font-weight: bold;
    @media screen and (max-width: 1314px){
        font-size: 3.8rem;
    }
    @media screen and (max-width: 1300px){
        font-size: 3.5rem;
    }
    @media screen and (max-width: 1148px){
        font-size: 2.3rem;
    }
`

export const VideoLinkBox = styled.div`
    width: 10rem;
    height: 10rem;
    background: linear-gradient(242deg, #ffffff40, #ffffff17);
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    right: 4rem;
    @media screen and (min-width: 1148px){
        width: 8rem;
        height: 8rem;
    }
`

export const ArrowImg = styled.img`
    width: 50%!important;
`

export const VideoLinkHref = styled.a`
`
export const DemoLink = styled(Link)`
`

