import React from 'react'
import FbIcon from './facebook-f-black.svg'
import TwtrIcon from './twitter-black.svg'
import linkedinIcon from './linkedin-black.svg'
import YtbIcon from './youtube-black.svg'
import {
    SixthTeam,
    TeamCol,
    SixthSec,
    SixthSecFluidRow,
    SixthSecFluidCol,
    SixthSecContainer,
    SixthSecContainerRow,
    SixthSecCol,
    SixthSecHeading,
    SixthSecP,
    TeamMember,
    TeamMemberCol,
    TeamMemberName,
    TeamMemberTitle,
    SocialMediaLink,
    SMul,
    SMli,
    TeamPhoto,
    TeamPhotoImg,
    SocialIcon

} from './SixthSectionElements'

const SixthSection = () => {
    const team = [
        {
            TeamName: 'Ray Mangon',
            TeamTitle: 'Founder & CEO',
            TeamImage: './images/Ray.jpg',
            facebook: '',
            twiter: '',
            instagram: ''
        },
        {
            TeamName: 'Jonathan Boarman',
            TeamTitle: 'CTO',
            TeamImage: './images/Jonathan.jpg',
            facebook: '',
            twiter: '',
            instagram: ''
        },
        {
            TeamName: 'Bobby Lane',
            TeamTitle: 'CFO',
            TeamImage: './images/Bobby.jpg',
            facebook: '',
            twiter: '',
            instagram: ''
        },
        {
            TeamName: 'Maheshwar Kuchana',
            TeamTitle: 'AI Engineer',
            TeamImage: './images/Maheshwar.jpg',
            facebook: '',
            twiter: '',
            instagram: ''
        },
        {
            TeamName: 'Chidozie Mgbemena',
            TeamTitle: 'Data Consultant',
            TeamImage: './images/Chidozie.jpg',
            facebook: '',
            twiter: '',
            instagram: ''
        }
    ]
    return (
        <SixthSec className="container-fluid">
            <SixthSecFluidRow className="row">
                <SixthSecFluidCol className="col-md-12">
                    <SixthSecContainer className="container">
                        <SixthSecContainerRow className="row">
                            <SixthSecCol className="col-md-4">
                                <SixthSecHeading>
                                    Meet our team
                                </SixthSecHeading>
                                <SixthSecP>
                                    From a solid foundation, we continue building a team of highly ambitious and experienced individuals, that are focused on the perfect balance between human engagement and technology
                                </SixthSecP>
                            </SixthSecCol>
                            <SixthSecCol className="col-md-8">
                                <SixthTeam className="row">
                                    {team.map((item, key) => {
                                        return <TeamCol className="ml-5 col-6 col-sm-6 col-md-6 col-lg-4" key={key}>
                                            <TeamMember className="row">
                                                <TeamMemberCol className="col-12 col-md-5">
                                                    <TeamPhoto>
                                                        <TeamPhotoImg src={item.TeamImage} />
                                                    </TeamPhoto>
                                                </TeamMemberCol>
                                                <TeamMemberCol className="col-md-7">
                                                    <TeamMemberName>{item.TeamName}</TeamMemberName>
                                                    <TeamMemberTitle>{item.TeamTitle}</TeamMemberTitle>
                                                    <SocialMediaLink>
                                                        <SMul>
                                                            <SMli><SocialIcon src={FbIcon} /></SMli>
                                                            <SMli><SocialIcon src={TwtrIcon} /></SMli>
                                                            <SMli><SocialIcon src={linkedinIcon} /></SMli>
                                                        </SMul>
                                                    </SocialMediaLink>
                                                </TeamMemberCol>
                                            </TeamMember>
                                        </TeamCol>

                                    })}

                                </SixthTeam>
                            </SixthSecCol>
                        </SixthSecContainerRow>
                    </SixthSecContainer>
                </SixthSecFluidCol>
            </SixthSecFluidRow>
        </SixthSec>
    )
}

export default SixthSection
