import styled from "styled-components";


export const SecondSec = styled.div`
    padding: 7.8rem 18rem 7.8rem 0px!important;
    @media screen and (max-width: 1148px){
        padding: 0px!important;
    }
`
export const SecondSecRow = styled.div`
    margin-right: 18rem;    
    justify-content: center!important;
`
export const SecondSecCol1 = styled.div`
    align-items: center;
    display: flex!important;
    justify-content: center;
    @media screen and (max-width: 991px){
        display: none!important;
    }
`
export const SecondSecCol = styled.div`
    padding: 1rem;
    @media screen and (max-width: 991px){
        width: 43%;
        margin: 1rem;
    }
`
export const SecondSecColBox = styled.div`
    width: 100%;
    height: 27rem;
    border-radius: 1.5rem;
    background: #c5c5c52e;
    backdrop-filter: blur(2rem);
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-direction: column;
`

export const SecondSecImg = styled.img`
    width: 50%;
`

export const SecondHeading = styled.h3`
    font-size: 2rem;
    font-weight: bold;
    color: #372B7B;
    margin-top: 2rem;
`

export const SecondSecStripImg = styled.img`
    width: 100%;
`
