import styled from "styled-components";


export const FourthSec = styled.div`

`

export const FourthSecRow = styled.div`

`

export const FourthSecCol = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    overflow: hidden;
    padding: 7.6rem 0rem 14.6rem 0rem;
    @media screen and (max-width:1148px){
        padding:0rem;        
    }
`

export const FourthHeading = styled.h3`
    transform: rotate(270deg);
    font-size: 4rem;
    font-weight: bold;
    white-space: nowrap;
    color:  #1a1a1a00;
    margin-left: 23rem;
    position: relative;
    display:flex;
    justify-content: center;
    align-item: center;
    z-index: 2;
    
    &:before{
        content: 'Now more than ever';
        position: absolute;
        left: 12%;
        top: 7px;
        color: #372B7B;
        font-size: 3rem;
        z-index: 1;
    }

    @media screen and (max-width:1500px){     
        margin-left: 0rem;
    }
     @media screen and (max-width:575px){     
       transform: rotate(0deg);
       margin-top: 5rem;
    }
`
export const FourthSecSlider = styled.div`

`
export const FourthSecSliderRow = styled.div`
    flex-wrap: nowrap!important;
    position: absolute;
    left: 0px;
    top: 12rem;
`
export const FourthSecSliderCol = styled.div`

`
export const FourthSecSliderColContainer = styled.div`
    padding: 3rem;
    border-radius: 2rem;
    background: linear-gradient(45deg, #ffffff1a, #ffffff0a);
    backdrop-filter: blur(2rem);
    border: 1px solid #ffffff3b;
`
export const FourthSecSliderColHeading = styled.h3`
    font-size: 2.4rem;
    font-weight: bold;
`
export const FourthSecSliderP = styled.p`
    font-size: 1.5rem;
    margin-top: 1rem;
`






