import React from 'react'
import LogoImg from './images/logo-img.png'
import {LogoContainer,LogoImgs,LogoA} from './LogoSecElements'
const LogoSec = () => {
    return (
        <LogoContainer>
            <LogoA to="/"><LogoImgs src={LogoImg}/></LogoA>
        </LogoContainer>
    )
}

export default LogoSec
